@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
body {
  font-family: Helvetica Neue, Helvetica, Arial, Liberation Sans, Roboto, Noto,
    sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  transition: all 0.3s ease-in-out;
  overflow: auto;
  min-height: 100vh;
  padding: 0;
  margin: 0;
}

main {
  width: clamp(300px, 70%, 800px);
  height: auto;
  margin: auto;
  border: 1px solid black;
  padding: 50px;
  box-shadow: 1px 1px 10px black;
  border-radius: 10px;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 80px 20px;
  margin: 0 auto;
  max-width: 1060px;
}

.header h1 {
  font-size: 4.75rem;
  letter-spacing: -1px;
  font-weight: 700;
  line-height: 1.06;
  color: #2d2d2d;
  text-align: center;
}

.header p {
  font-size: 1.875rem;
  letter-spacing: -0.12px;
  font-weight: 400;
  line-height: 1.14;
  color: #4b4b4b;
}

.header button {
  background-color: #085ff7;
  color: #fff;
  text-decoration: none;
  border: 0.125rem solid #085ff7;
  border-radius: 6.25rem;
  background-repeat: repeat-x;
  cursor: pointer;
  padding: 1rem 1.5rem 1rem 1.5rem;
  display: block;
  width: 100%;
  font-size: 1rem;
  letter-spacing: 0;
  font-weight: 700;
  line-height: 1.125rem;
  max-width: 21.9375rem;
  margin-top: 40px;
}

.header button:hover {
  background-color: #0044e3;
  border-color: #0044e3;
}

@media screen and (max-width: 1024px) {
  .header h1 {
    font-size: 2.625rem;
  }

  .header p {
    font-size: 1.4375rem;
  }
}

.info {
  transition: all 0.3 ease-in-out;
}

hr {
  border: 1px dashed gray;
  margin-bottom: 25px;
}

.info h2 {
  margin-bottom: 5px;
  color: #085ff7;
  font-size: 1.875rem;
}

form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: row wrap;
  font-size: 1.4375rem;
}

form input {
  width: 48%;
  margin-bottom: 30px;
  font-size: 1.1rem;
  height: 30px;
  border: 1px solid transparent;
  border-bottom: 2px solid black;
  transition: all 0.3s ease-in-out;
  outline: none;
  color: #4b4b4b;
}

form input:focus {
  border-bottom: 2px solid blue;
}

form input:hover {
  border-bottom: 2px solid blue;
}

form textarea {
  width: 100%;
  font-size: 1.1rem;
  border: 1px solid transparent;
  border-bottom: 2px solid black;
  transition: all 0.3s ease-in-out;
  outline: none;
  color: #4b4b4b;
}

form textarea:focus {
  border-bottom: 2px solid blue;
}

form textarea:hover {
  border-bottom: 2px solid blue;
}

form input[type='checkbox'] {
  cursor: pointer;
}

label {
  font-size: 16px;
  margin-bottom: 30px;
}

.buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

button {
  background-color: #085ff7;
  color: #fff;
  text-decoration: none;
  border: 0.125rem solid #085ff7;
  border-radius: 6.25rem;
  background-repeat: repeat-x;
  cursor: pointer;
  padding: 1rem 1.5rem 1rem 1.5rem;
  display: block;
  width: 100%;
  font-size: 1rem;
  letter-spacing: 0;
  font-weight: 700;
  line-height: 1.125rem;
  max-width: 20rem;
  margin-top: 40px;
}

button:hover {
  background-color: #0044e3;
  border-color: #0044e3;
}

.extra {
  background-color: white;
  color: #085ff7;
  cursor: pointer;
  text-decoration: underline;
  padding: 0;
  border: none;
  max-width: 9rem;
}

.extra:hover {
  color: #0044e3;
  background-color: white;
}

.delete {
  margin-top: -20px;
  margin-bottom: 15px;
}

.delete-btn {
  background-color: crimson;
  border-color: crimson;
  padding: 0.8rem;
  max-width: 8rem;
}

.delete-btn:hover {
  background-color: rgb(238, 20, 64);
  border-color: rgb(238, 20, 64);
}

/* Education form */
.education-form input[type='text'] {
  width: 100%;
}

.date {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.education-form .date .date-field {
  width: 40%;
}

.education-form input[type='date'] {
  width: 40%;
}

.ongoing {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 1024px) {
  form input {
    width: 100%;
  }

  .buttons {
    flex-flow: column-reverse;
  }

  button {
    max-width: 100%;
  }

  .education-form .date .date-field {
    width: 45%;
  }
}

.pdf {
  height: auto;
  margin: auto;
  box-shadow: 1px 1px 10px black;
  min-width: 750px;
  max-width: 750px;
  font-family: 'Nunito', sans-serif;
}

header {
  padding: 10px;
  padding-bottom: 1px;
  height: 141px;
  background-color: rgb(20, 62, 114);
}

.pdf h1 {
  color: rgb(238, 238, 238);
  font-size: 1.9rem;
  letter-spacing: -0.12px;
  font-weight: 700;
  line-height: 1.14;
  margin-top: 35px;
}

.pdf h3 {
  color: rgb(238, 238, 238);
  font-size: 1.3rem;
  letter-spacing: -0.12px;
  font-weight: 700;
  line-height: 1.14;
}

.pdf h2 {
  color: rgb(20, 62, 114);
  font-size: 1.3rem;
  letter-spacing: -0.12px;
  font-weight: 400;
  line-height: 1.14;
}

.view {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  margin-bottom: 10px;
}

.view-date {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: -25px;
  margin-right: 20px;
}

.view p {
  margin: 0px;
  padding: 5px;
}

.view span {
  padding: 5px;
}

section {
  padding: 15px;
}

.pdf-grid {
  display: grid;
  grid-template-columns: 3fr 1.5fr;
  grid-template-rows: 1fr;
}

.personal-info {
  background-color: whitesmoke;
  padding: 10px;
}

.pdf-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

